'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { Menu, X, Settings, LogOut, BarChart3, Table2, Edit3, Send, ShoppingCart } from 'lucide-react';

export default function AdminLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const [sidebarOpen, setSidebarOpen] = useState(true);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const checkMobile = () => {
      setIsMobile(window.innerWidth < 1024);
    };
    checkMobile();
    window.addEventListener('resize', checkMobile);
    return () => window.removeEventListener('resize', checkMobile);
  }, []);

  const navItems = [
    { href: '/peep/admin', label: 'Dashboard', icon: BarChart3 },
    { href: '/peep/admin/evidence-table', label: 'Evidence', icon: Table2 },
    { href: '/peep/admin/zone-editor', label: 'Zones', icon: Edit3 },
    { href: '/peep/admin/authority-panel', label: 'Authorities', icon: Send },
    { href: '/peep/admin/marketplace', label: 'Marketplace', icon: ShoppingCart },
  ];

  if (isMobile) {
    return (
      <div className="min-h-screen flex items-center justify-center px-4 bg-gray-50" style={{ backgroundColor: 'var(--peep-neutral-1)' }}>
        <div className="text-center">
          <h1 className="text-2xl font-bold mb-4 text-gray-900">Desktop Only</h1>
          <p className="text-gray-600 mb-6">
            Switch to desktop to access the admin dashboard (1366px+ width recommended).
          </p>
          <button
            onClick={() => {
              if (window.history.length > 1) window.history.back();
              else window.location.href = '/peep/user';
            }}
            className="py-2 px-4 rounded-lg border border-gray-300 hover:bg-gray-100"
          >
            Go Back
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="flex h-screen bg-white" style={{ backgroundColor: 'var(--peep-neutral-1)' }}>
      {/* Sidebar */}
      <aside
        className={`${
          sidebarOpen ? 'w-64' : 'w-20'
        } bg-white border-r border-gray-200 flex flex-col transition-all duration-300 overflow-y-auto`}
      >
        {/* Logo */}
        <div className="p-4 flex items-center justify-between border-b border-gray-200 h-16">
          {sidebarOpen && (
            <h1 className="text-xl font-bold" style={{ color: 'var(--peep-primary)' }}>
              PEEP
            </h1>
          )}
          <button
            onClick={() => setSidebarOpen(!sidebarOpen)}
            className="p-1 hover:bg-gray-100 rounded-lg"
          >
            {sidebarOpen ? <X className="w-5 h-5" /> : <Menu className="w-5 h-5" />}
          </button>
        </div>

        {/* Nav items */}
        <nav className="flex-1 p-4 space-y-2">
          {navItems.map((item) => {
            const Icon = item.icon;
            return (
              <Link
                key={item.href}
                href={item.href}
                className="flex items-center gap-3 px-3 py-2 rounded-lg hover:bg-gray-100 transition-colors"
                title={item.label}
              >
                <Icon className="w-5 h-5 flex-shrink-0" style={{ color: 'var(--peep-primary)' }} />
                {sidebarOpen && <span className="text-sm font-medium text-gray-700">{item.label}</span>}
              </Link>
            );
          })}
        </nav>

        {/* Footer */}
        <div className="p-4 border-t border-gray-200 space-y-2">
          <button
            className="flex items-center gap-3 px-3 py-2 w-full rounded-lg hover:bg-gray-100 text-gray-700 text-sm"
            title="Settings"
          >
            <Settings className="w-5 h-5 flex-shrink-0" />
            {sidebarOpen && <span>Settings</span>}
          </button>
          <button
            className="flex items-center gap-3 px-3 py-2 w-full rounded-lg hover:bg-red-50 text-red-600 text-sm"
            title="Logout"
          >
            <LogOut className="w-5 h-5 flex-shrink-0" />
            {sidebarOpen && <span>Logout</span>}
          </button>
        </div>
      </aside>

      {/* Main content */}
      <div className="flex-1 flex flex-col overflow-hidden">
        {/* Top bar */}
        <header className="h-16 bg-white border-b border-gray-200 px-6 flex items-center justify-between">
          <h1 className="text-lg font-semibold text-gray-900">Admin Dashboard</h1>
          <div className="flex items-center gap-4">
            <div className="text-sm text-gray-600">
              Logged in as <span className="font-medium">Admin User</span>
            </div>
          </div>
        </header>

        {/* Page content */}
        <main className="flex-1 overflow-y-auto bg-gray-50">
          {children}
        </main>
      </div>
    </div>
  );
}
